<script>
    import Clipboard from 'clipboard'
    let provider, signer, defaultAccount, networkVersion;
    let usdtContract, usdtAddress = '0x55d398326f99059fF775485246999027B3197955';
    let ftpContract, ftpAddress = '0x8a47d7C0E7E1e5069bf6f6FB9f0b1518da98913e';
    let lpContract, lpAddress = '0xbD117612257798d6d642be0622DC2842c23149BF';
    let nodeContract, nodeAddress = '0xf0FA00f54Db8e2AF14D742aae512000Ab7482c42';
    let yaContract, yaAddress = '0xa14EBcFfF224257FC35d4B046f1CD6A324440E92';
    let chiAddress = "0x5bb28c22AE9386b65DABB475DF2f9090B96dc57F";
    let lpUrl = "https://pancakeswap.finance/swap?inputCurrency=0x55d398326f99059ff775485246999027b3197955&outputCurrency=0x8a47d7c0e7e1e5069bf6f6fb9f0b1518da98913e";
    let bnbUrl = 'https://bscscan.com/address/';
    let baseUrl = "https://ftprediction.github.io";
    export default {
        data() {
            return {
                lang: 'en',
                lang_list: {},
                address: '',
                short_address: '',
                parents:'',
                disable: false,
                show: false,
                meanShow: false,
                type: '',
                num: 1000000,
                isNode:false,
                parentsNode:false,
                ftp:'',
                nodeInfo:{
                  start:0,
                  end:49,
                  rate:0,
                  profit:0,
                },
                nodedisable:false,
                npdisable:false,
                ftpYu:{}, //
                ftpYuInfo:{
                    lun:'',
                    rate:'0',
                    canGet:0,
                    shen:0,
                    price:0,
                }, //
                yuNum:'',
                ysdisable:false,
                activePrice:'',
                ftpBanner:0,
                lpbanner:0,
                activeTime:1,
                activeProfit:0,
                ftpdisable:false,
                fudisable:false,
                gfpdisable:false,
                ftpInfo:{
                    profit:0,
                    sum:0,
                    suoProfit:0,
                    num:0,
                },
                activeLpPrice:'',
                lpdisable:false,
                activeLpProfit:0,
                lpInfo:{
                    lpAmount:0,
                    LPprofit:0,
                },
                lp_rate:0,
                ludisable:false,
                chiBanner:0,
                nodeId:0,
                nodeUrl:0,
            }
        },
        created: function () {
            this.checkNet();
            this.ftp = ftpAddress;
        },
        async mounted() {
            this.parents = this.$route.query.p_address?this.$route.query.p_address:this.getUrlParam('p_address');
            window.addEventListener('load', async () => {
                // Modern dapp browsers...
                if (window.ethereum) {
                    try {
                        window.web3 = new Web3(ethereum);
                        // Request account access if needed
                        await ethereum.enable();
                        // Acccounts now exposed
                        web3.eth.sendTransaction({/* ... */});
                    } catch (error) {
                        // User denied account access...
                    }
                }
                // Legacy dapp browsers...
                else if (window.web3) {
                    window.web3 = new Web3(web3.currentProvider);
                    // Acccounts always exposed
                    web3.eth.sendTransaction({/* ... */});
                }
                // Non-dapp browsers...
                else {
                    console.log('Non-Ethereum browser detected. You should consider trying MetaMask!');
                }
            });
        },
        methods: {
            toWhite(){
                window.open(baseUrl+"/ftp.pdf")
            },
            toTrade(){
               window.open(lpUrl)
            },
            comeIn(){
              this.$toast('');
            },
            hiddleShow(){
                this.show=false;
            },
            lookNft(){
                this.show=true;
            },
            async lpUnstake(event){
                let _this = this;
                if(this.lpInfo.lpAmount<=0){
                    this.$toast('LP');
                    return false;
                }
                if(_this.ludisable){
                    return false;
                }
                _this.ludisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try{
                    const gasPrice = await provider.getGasPrice()
                    let res = await yaContract.redeemLP({
                        gasPrice: gasPrice,
                        gasLimit:250000
                    })
                    if(res){
                        _this.ludisable = false;
                        _this.$toast('');
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }
                }catch (e) {
                    _this.$toast('');
                    _this.ludisable = false;
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            async submitLp(event){
                let _this = this;
                const gasPrice = await provider.getGasPrice();
                if(this.activeLpPrice>this.lpbanner){
                    this.$toast('Lp');
                    return false;
                }
                if(this.activeLpPrice<=0){
                    this.$toast('Lp0');
                    return false;
                }
                if(this.lpdisable){
                    return false;
                }
                this.lpdisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                let etherNum = ethers.utils.parseEther (parseFloat(Math.floor(parseFloat(this.activeLpPrice)* 100)/100).toString());
                await lpContract.allowance(defaultAccount,yaAddress).then(async res =>{
                    if(parseFloat(ethers.utils.formatEther(res))>0){
                        let tx1 = await lpContract.approve(yaAddress,ethers.utils.parseEther('0'),{
                            gasPrice: gasPrice,
                            gasLimit:250000
                        })
                    }
                    try{
                        let tx = await lpContract.approve(yaAddress,etherNum,{
                            gasPrice: gasPrice,
                            gasLimit:250000
                        })
                        if(tx){
                            let res = await yaContract.investmentFPTLP(etherNum,{
                                gasPrice: gasPrice,
                                gasLimit:400000
                            })
                            if(res){
                                _this.lpdisable = true;
                                _this.$toast('');
                                setTimeout(function () {
                                    location.reload();
                                },3000)
                            }
                        }
                    }catch (e) {
                        _this.lpdisable = true;
                        _this.$toast('');
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }

                }).catch(err => {
