                                let nowTime =parseInt((new Date().getTime())/1000);
                                if(nowTime>=endTime){
                                    _this.nodeInfo.profit = lastProfit*0.1;
                                }
                            }
                        }).catch(err => {
                            console.log(err);
                            return;
                        });
                    }
                }).catch(err => {
                    console.log(err);
                    return;
                });
                // 
                if(_this.parents){
                    await nodeContract.getIdByAddr(_this.parents).then(res => {
                        let id = ethers.utils.formatEther(res)*1000000000000000000;
                        if(id>0){
                           _this.parentsNode = true;
                        }
                    }).catch(err => {
                        console.log(err);
                        return;
                    });
                }
               /* await nodeContract.administrators().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.nodeInfo.end = ethers.utils.formatEther(res)*1000000000000000000
                    console.log(_this.nodeInfo)
                }).catch(err => {
                    console.log(err);
                    return;
                });*/

            },
            async getFtpBanner() {  //
                let _this = this;
                await ftpContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.ftpBanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            async getChiBanner() {  //
                let _this = this;
                await ftpContract.balanceOf(chiAddress).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    let num = Math.floor(parseFloat(ethers.utils.formatEther(res)));
                    if( num>1000){
                        _this.chiBanner = (parseFloat(num)/1000).toFixed(2)+'K';
                    }
                    if(num>1000000){
                        _this.chiBanner = (parseFloat(num)/1000000).toFixed(2)+'M';
                    }
                    console.log(_this.chiBanner)
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            async getLpBanner() {  //
                let _this = this;
                await lpContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.lpbanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
        }
    }
</script>


